@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Oswald', sans-serif,BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

p {
  font-weight: 300; }

.Auth {
  display: block; }

.Home {
  display: block; }

.MyNavbar {
  display: block;
  box-shadow: inset 4px 4px 15px rgba(255, 255, 255, 0.1), inset -4px -4px 15px rgba(255, 255, 255, 0.1); }

.nav-link {
  text-align: right; }

.SquadListCard {
  display: grid; }

.bg-black {
  background-color: black; }

.buttonRow {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  border-top: 1px solid black; }

.slCard {
  box-shadow: inset 4px 4px 15px rgba(255, 255, 255, 0.1), inset -4px -4px 15px rgba(255, 255, 255, 0.1); }

.slCardBtn {
  width: 100%;
  border-radius: 0; }

@media (min-width: 640px) {
  .buttonRow {
    flex-flow: row nowrap; } }

.SquadManagerModal {
  display: block; }

.SquadManager {
  display: block; }

.newSquadListCard {
  display: flex;
  justify-content: center; }

.SquadMember {
  display: block; }

.noCharacterinSquad {
  width: 40px;
  height: 40px;
  background-color: rgba(187, 187, 187, 0.15);
  border: 2px solid rgba(187, 187, 187, 0.15);
  border-radius: 50%; }

.modal-header .close {
  color: white; }

@media only screen and (min-width: 768px) {
  .noCharacterinSquad {
    height: 45px;
    width: 45px; } }

@media only screen and (min-width: 992px) {
  .noCharacterinSquad {
    height: 55px;
    width: 55px; } }

@media only screen and (min-width: 1200px) {
  .noCharacterinSquad {
    height: 65px;
    width: 65px; } }

.SquadRow {
  display: flex;
  box-shadow: inset 4px 4px 15px rgba(218, 53, 53, 0.1), inset -4px -4px 15px rgba(255, 255, 255, 0.1);
  flex-direction: column; }

.col-3 {
  padding-left: 0; }

.squadBtn {
  padding: auto 0px; }

.squadRowButtons {
  display: flex;
  flex-direction: row;
  width: 18.5%;
  justify-content: right;
  align-items: center; }

.squadRowImages {
  display: flex;
  flex-direction: row;
  width: 80%;
  justify-content: left;
  margin-bottom: 5px; }

.squadRowLeftColumn {
  display: flex;
  flex-direction: row; }

.squadRowRightColumn {
  display: flex;
  flex-direction: row; }

.squadRowTitle {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: left; }

.toonImg {
  height: 40px;
  width: 40px;
  border-radius: 50px;
  border: 2px solid #9B47E4; }

@media only screen and (min-width: 576px) {
  .SquadRow {
    flex-direction: row; }
  .squadRowButtons {
    width: 33.334%;
    align-items: center; }
  .squadRowImages {
    width: 66.667%;
    justify-content: center;
    margin-bottom: 5px; }
  .squadRowLeftColumn {
    width: 25%;
    align-items: center; }
  .squadRowRightColumn {
    width: 75%; }
  .squadRowTitle {
    width: 100%;
    justify-content: center; } }

@media only screen and (min-width: 768px) {
  .col-3 {
    padding-left: 0;
    padding-left: initial; }
  .squadRowButtons {
    width: 25%;
    align-items: center; }
  .toonImg {
    height: 45px;
    width: 45px; } }

@media only screen and (min-width: 992px) {
  .toonImg {
    height: 55px;
    width: 55px; } }

@media only screen and (min-width: 1200px) {
  .toonImg {
    height: 65px;
    width: 65px; } }

.SquadListModal {
  display: block; }

.characterPool {
  display: flex;
  flex-flow: row wrap;
  overflow: scroll;
  padding-bottom: 10px; }

.modal {
  font-size: .9rem; }
  .modal input {
    font-size: .9rem; }
  .modal select {
    font-size: .8rem; }

.noCharacterSelected {
  width: 40px;
  height: 40px;
  background-color: #bbb;
  border-radius: 50%; }

.slmBody {
  box-shadow: inset 4px 4px 15px rgba(218, 53, 53, 0.1), inset -4px -4px 15px rgba(255, 255, 255, 0.1); }

.squadForm {
  max-height: 277px; }

.squadFormButtons {
  display: flex;
  flex-direction: row; }

.squadFormLeft {
  overflow: hidden; }

.squadFormLeftTop {
  display: flex;
  flex-direction: column; }

.squadFormLeftBottom {
  display: flex;
  flex-direction: column; }

.squadFormRight {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.squadFormRightBottom {
  display: flex;
  flex-direction: column;
  margin-bottom: 0; }

.squadFormRightTop {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 15px 0 0 0; }

.squadFormToon {
  display: flex;
  flex-direction: column;
  align-items: center; }

.toonModalImg {
  height: 40px;
  width: 40px;
  border-radius: 50px;
  border: 2px solid #9B47E4; }

.toonPoolCard {
  display: flex;
  max-width: 40px;
  flex-flow: column wrap;
  text-align: center;
  align-items: center; }

.toonPoolName {
  font-size: .7rem;
  font-weight: 300; }

@media (min-width: 576px) {
  .modal-dialog {
    max-width: 650px; } }

@media only screen and (min-width: 768px) {
  .noCharacterSelected {
    width: 45px;
    height: 45px; }
  .toonModalImg {
    height: 45px;
    width: 45px; }
  .toonPoolCard {
    max-width: 45px; } }

@media only screen and (min-width: 992px) {
  .noCharacterSelected {
    width: 55px;
    height: 55px; }
  .toonModalImg {
    height: 55px;
    width: 55px; }
  .toonPoolCard {
    max-width: 55px; } }

@media only screen and (min-width: 1200px) {
  .noCharacterSelected {
    width: 65px;
    height: 65px; }
  .toonModalImg {
    height: 65px;
    width: 65px; }
  .toonPoolCard {
    max-width: 65px; } }

.SquadList {
  display: block; }

.squadListTitleCard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.squadListTitleSpace {
  width: 20%; }

.squadListTitle {
  width: 100%; }

.squadListTitleButton {
  width: 50%;
  margin-bottom: 16px; }

.App {
  text-align: center;
  background-color: black;
  box-shadow: inset 0px 0px 10px rgba(255, 255, 255, 0.1), inset 0px 0px -10px rgba(255, 255, 255, 0.1);
  color: whitesmoke; }

h1 {
  font-size: 1.25rem; }

h5 {
  font-size: 1rem; }

@media only screen and (min-width: 768px) {
  h1 {
    font-size: 1.50rem; }
  h5 {
    font-size: 1.25rem; } }

@media only screen and (min-width: 992px) {
  h1 {
    font-size: 1.75rem; }
  h5 {
    font-size: 1.5rem; } }

@media only screen and (min-width: 1200px) {
  h1 {
    font-size: 2rem; }
  h5 {
    font-size: 1.75rem; } }

